        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >

        <el-form-item>
          <template slot="label">
            <div class="bigText2">{{tableStr1}}</div>
            <div class="greenTextSmall">必填</div>
          </template>
          <el-row class="tableRow">
            <el-radio v-model="form.isFixed" :label="true">浮动单价</el-radio>
            <el-radio v-model="form.isFixed" :label="false">固定价格</el-radio>
          </el-row>
          <el-row v-show="!form.isFixed">
            <el-row type="flex" justify="left" :gutter="40">
              <el-col :span="10">
                <div class="normalText">您的报价</div>
                <div>
                  <el-input v-model="form.offerPrice" :placeholder="String(defaultPrice)"></el-input>
                </div>
              </el-col>
              <el-col>
                <div class="normalText">当前市场最低价</div>
                <div class="greenTextBig">{{lowestMarketPrice}}</div>
              </el-col>
            </el-row>
          </el-row>
          <el-row v-show="form.isFixed">
            <el-row type="flex" justify="center" :gutter="40">
              <el-col :span="10">
                <div class="normalText">市场价对标交易所</div>
                <div>
                  <el-select v-model="priceModel" style="width:100%">
                    <el-option value="bitstamp" label="Bitstamp"></el-option>
                    <el-option value="coinmarketcap" label="Coinmarketcap"></el-option>
                    <el-option value="bitfinex" label="Bitfinex"></el-option>
                    <el-option value="average" label="各平台最新成交均价（Average）"></el-option>
                    <el-option value="binance" label="Binance"></el-option>
                  </el-select>
                </div>
              </el-col>
              <el-col :span="7">
                <div class="normalText">您的报价</div>
                <div>
                  <el-input v-model="floatPrice" placeholder="defaultPrice"></el-input>
                </div>
              </el-col>
              <el-col :span="7">
                <div class="normalText">当前市场最低价</div>
                <div class="greenTextBig">{{lowestMarketPrice}}</div>
              </el-col>
            </el-row>

            <el-row type="flex" justify="left" :gutter="40" class="tableRow">
              <el-col>
                <div class="normalText">
                  <span class="greenTextSmall"></span>
                </div>
              </el-col>
            </el-row>
            <el-row type="flex" justify="left" :gutter="40" class="tableRow">
              <el-col :span="16">
                <div class="normalText">
                  <el-slider v-model="form.offerPriceRate" show-input :min="-70" :max="70"></el-slider>
                </div>
                <div class="normalText">
                  {{ "计价公式："+String(lowestMarketPrice)+" CNY * "+ String((100+form.offerPriceRate)/100)+" = "+String(floatPrice) +" CNY"}}
                  <span
                    class="greenTextSmall"
                  >(溢价率仅到小数点后3位)</span>
                </div>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <div class="normalText">最小报价</div>
                <div>
                  <el-input v-model="form.minPrice" placeholder="0"></el-input>
                </div>
              </el-col>
            </el-row>
          </el-row>
        </el-form-item>
        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <template slot="label">
            <div class="bigText2">{{"交易方式"}}</div>
            <div class="greenTextSmall">必填</div>
            <!-- {{form.checkList}} -->
          </template>
          <el-row class="tableRow">
            <el-checkbox-group v-model="form.checkList">
              <el-checkbox label="银行转账"></el-checkbox>
              <el-checkbox label="支付宝"></el-checkbox>
              <el-checkbox label="微信"></el-checkbox>
              <el-checkbox label="PayPal"></el-checkbox>
            </el-checkbox-group>
          </el-row>
        </el-form-item>
        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <template slot="label">
            <div class="bigText2">{{"交易限额"}}</div>
            <div class="greenTextSmall">必填</div>
            <!-- {{form.checkList}} -->
          </template>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <el-col :span="11">
              <div class="normalText">
                最小限额
                <span class="greenText">（一次交易的最低的交易限制）</span>
              </div>
              <div>
                <el-input v-model="form.minPrice" placeholder="0"></el-input>
              </div>
            </el-col>
            <el-col :span="11">
              <div class="normalText">
                最大限额
                <span class="greenText">（一次交易的最高的交易限制）</span>
              </div>
              <div>
                <el-input v-model="form.maxPrice" placeholder="0"></el-input>
              </div>
            </el-col>
          </el-row>
        </el-form-item>
        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <template slot="label">
            <div class="bigText2">{{"交易说明"}}</div>
            <div class="greenTextSmall">所有用户可见</div>
            <div class="greenTextSmall">建议填写</div>
            <!-- <div class="greenTextSmall">{{form.tinymceHtml}}</div> -->
            <!-- {{form.checkList}} -->
          </template>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <el-col>
              <tinymce-editor v-model="form.tinymceHtml" ref="editor"></tinymce-editor>
            </el-col>
          </el-row>
        </el-form-item>

        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <template slot="label">
            <div class="bigText2">{{"交易说明"}}</div>
            <div class="greenTextSmall">所有用户可见</div>
            <div class="greenTextSmall">建议填写</div>
            <!-- <div class="greenTextSmall">{{form.tinymceHtml}}</div> -->
            <!-- {{form.checkList}} -->
          </template>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <ul>
              <li>OTCBTC 为了保证用户的交易安全，将采用数字货币托管系统。严禁绕过平台私下交易，违者将自行承担损失，且永久封号。</li>
              <li>请在交易说明及交易备注中，详细写下您所希望的交易条件，以及与您交易所需注意的事项，明确的信息将提升您的交易成功机率及评价。</li>
              <li>请勿在 OTCBTC 尝试欺诈行为，违者将会导致账号被冻结，并承担法律责任。</li>
              <li>发布广告后请履行契约精神，恶意抬价或者是反悔，被投诉将冻结账户 3-14 天不等。</li>
            </ul>
          </el-row>
          <hr
            style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
            color="#eef1f6"
            size="3"
          >
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <el-col>
              <div class="bigText2">注意！注意！注意！务必详细阅读以下信息，以确保您的交易安全</div>
            </el-col>
          </el-row>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <ul>
              <li>交易过程中，请先确认款项入帐后再释放数字货币。</li>
              <li>交易过程中，不要透露与您账户安全有关的敏感资讯。</li>
              <li>不要直接透露你的电子邮箱给买家，否则有可能会收到诈骗信件，及对账户安全造成危害。</li>
              <li>永远确保你在正确的 OTCBTC 网站上，请将网站记录在书签上，避免误闯钓鱼网站。</li>
              <li>OTCBTC 员工将不会以官方名义与您进行交易，请不要与自称是 OTCBTC 的员工交易，避免利益损失。</li>
            </ul>
          </el-row>
        </el-form-item>
      </el-form>
    </el-row>
  </section>
</template>

<script>
import TinymceEditor from "./tinymce-editor";

export default {
  components: {
    TinymceEditor
  },
  data() {
    return {
      priceModel: "average",
      isLogin: false,
      lowestMarketPrice: 26012.921,
      tableStr1_1: "出售价设置 ",
      tableStr1_2: "收购价设置 ",
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
        //zhende
        isBuy: true,
        offerType: "",
        currency: "",
        radio: true,
        offerPrice: 26012.921,
        isFixed: true,
        offerPrice_2: 26012.921,
        offerPriceRate: 0,
        minPrice: 100,
        maxPrice: 1000,
        checkList: [],
        tinymceHtml:
          "<p>1. 订单有效期为15分钟，请您及时在有效期内付款并点击「标记付款已完成」按钮，我才可以释放数字币给您</p><p> 2. 如果您无法在15分钟内完成付款，请点击「延长订单有效期」按钮</p><p> 3. 开始交易后数字币由系统锁定托管，请安心下单</p>"
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    },
    fomatFloat(value, n) {
      var f = Math.round(value * Math.pow(10, n)) / Math.pow(10, n);
      var s = f.toString();
      var rs = s.indexOf(".");
      if (rs < 0) {
        s += ".";
      }
      for (var i = s.length - s.indexOf("."); i <= n; i++) {
        s += "0";
      }
      return s;
    },
    formatTooltip(val) {
      return val / 100;
    }
  },
  mounted() {
    tinymce.init({});
    var user = sessionStorage.getItem("user");
    if (user) {
      this.isLogin = true;
    }
  },
  computed: {
    tableStr1: function() {
      return this.form.isBuy ? this.tableStr1_1 : this.tableStr1_2;
    },
    defaultPrice: function() {
      return parseFloat(
        this.fomatFloat(parseFloat(this.lowestMarketPrice) * 0.94, 3)
      );
    },
    floatPrice: function() {
      return parseFloat(
        this.fomatFloat(
          (this.lowestMarketPrice * (100 + this.form.offerPriceRate)) / 100,
          3
        )
      );
    }
  }
};
</script>
<style scoped>
.tableRow {
  margin-top: 11px;
}
.firstRow {
  margin: 2%;
}
.bigText {
  margin: 0 0 20px;
  border-left: 5px solid #43cb83;
  padding-left: 10px;
  font-size: 28px;
}
.normalText {
  margin: 0 0 10px;
}
.greenText {
  color: #2a985e;
  margin: 5px 0;
}
.greenTextBig {
  color: #2a985e;
  margin: 5px 0;
  font-size: 24px;
}
.greenTextSmall {
  color: #2a985e;
  margin: 5px 0;
  font-size: 10px;
}
.bigText2 {
  margin: 10px 0;
  font-size: 20px;
}
</style>



