                <el-option value="eosram">EOSRAM</el-option>
                <el-option value="oct">OCT</el-option>
                <el-option value="sac">SAC</el-option>
                <el-option value="bchsv">BCHSV</el-option>
                <el-option value="ae">AE</el-option>
                <el-option value="ait">AIT</el-option>
                <el-option value="bcd">BCD</el-option>
                <el-option value="bnb">BNB</el-option>
                <el-option value="btm">BTM</el-option>
                <el-option value="bto">BTO</el-option>
                <el-option value="dew">DEW</el-option>
                <el-option value="iost">IOST</el-option>
                <el-option value="kin">KIN</el-option>
                <el-option value="knc">KNC</el-option>
                <el-option value="lrc">LRC</el-option>
                <el-option value="mds">MDS</el-option>
                <el-option value="omg">OMG</el-option>
                <el-option value="pra">PRA</el-option>
                <el-option value="ruff">RUFF</el-option>
                <el-option value="snt">SNT</el-option>
                <el-option value="tnb">TNB</el-option>
                <el-option value="uip">UIP</el-option>
                <el-option value="xlm">XLM</el-option>
                <el-option value="zil">ZIL</el-option>
                <el-option value="zrx">ZRX</el-option>
              </el-select>
            </el-col>
            <el-col class="line" :span="2">{{"<=>"}}</el-col>
            <el-col :span="7">
              <el-select v-model="form.currency" placeholder="法币种类">
                <el-option value="cny" label="人民币"></el-option>
                <el-option value="krw" label="韩元"></el-option>
                <el-option value="jpy" label="日元"></el-option>
                <el-option value="usd" label="美元"></el-option>
                <el-option value="hkd" label="港币"></el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-form-item>
        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >

        <el-form-item>
          <template slot="label">
            <div class="bigText">{{"价格设定"}}</div>
          </template>

          <el-row type="flex" justify="left" :gutter="40" class="tableRow">
            <el-col :span="10">
              <el-input v-model="form.offerNum" placeholder="数量">
                <template slot="append">{{form.offerType}}</template>
              </el-input>
            </el-col>
            <el-col :span="10">
              <el-input v-model="form.offerPrice" placeholder="固定单价(不随市价浮动)">
                <template slot="append">{{"CNY"}}</template>
              </el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <template slot="label">
            <div class="bigText">{{"收款方式"}}</div>
          </template>
          <el-row class="tableRow">
            <el-checkbox-group v-model="form.checkList">
              <el-checkbox label="银行转账"></el-checkbox>
              <el-checkbox label="支付宝"></el-checkbox>
              <el-checkbox label="微信"></el-checkbox>
              <el-checkbox label="PayPal"></el-checkbox>
            </el-checkbox-group>
          </el-row>
        </el-form-item>
        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <template slot="label">
            <div class="bigText">{{"联系方式"}}</div>
          </template>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <el-col>
              <el-input v-model="form.contactDetails" placeholder="请填写联系方式"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <template slot="label">
            <div class="bigText">{{"购买限制"}}</div>
          </template>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <el-col>
              <div class="normalText">买家注册超过几天 (0 = 不限制)</div>
              <el-input v-model="form.buyersDay" placeholder="请填写联系方式"></el-input>
            </el-col>
          </el-row>
          <el-row class="tableRow">
            <el-checkbox-group v-model="form.checkList">
              <el-checkbox label="买家必须通过实名验证"></el-checkbox>
              <el-checkbox label="买家必须通过实名验证"></el-checkbox>
              <el-checkbox label="买家必须通过实名验证"></el-checkbox>
              <el-checkbox label="买家必须通过实名验证"></el-checkbox>
            </el-checkbox-group>
          </el-row>
        </el-form-item>

        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <template slot="label">
            <div class="bigText">{{"交易说明"}}</div>
          </template>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <el-col>
              <div class="normalText">交易说明</div>
            </el-col>
          </el-row>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <el-col>
              <el-input
                type="textarea"
                :autosize="{ minRows: 3, maxRows: 10}"
                v-model="form.textarea3"
              ></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <hr
          style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
          color="#eef1f6"
          size="3"
        >
        <el-form-item>
          <el-row class="tableRow" :gutter="40" type="flex" justify="left">
            <ul>
              <li>广告为一次性，交易完成后，广告随即过期。</li>
              <li>BTC 批量交易成交手续费为 0.5%，此费用由发布批量交易广告的用户承担。 点击查看详情。</li>
              <li>例如交易金额为 1 BTC，则批量交易成交手续费为 0.005 BTC。使用 OTB 支付 50% 折扣。</li>
              <li>发布广告后请履行契约精神，恶意抬价或者是反悔，被投诉将冻结账户 3-14 天不等。</li>
            </ul>
          </el-row>
        </el-form-item>
      </el-form>
    </el-row>
  </section>
</template>

<script>
import TinymceEditor from "./tinymce-editor";

export default {
  components: {
    TinymceEditor
  },
  data() {
    return {
      priceModel: "average",
      isLogin: false,
      lowestMarketPrice: 26012.921,
      tableStr1_1: "出售价设置 ",
      tableStr1_2: "收购价设置 ",
      form: {
        contactDetails: "",
        buyersDay: 0,
        textarea3:
          "1. 请在订单有效期内及时付款并点击「标记付款已完成」以锁定数字币 \n2. 请通过站内聊天或我留下的联系方式与我联系，我会尽快给您释放数字币",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        offerNum: "",
        //zhende
        isBuy: true,
        offerType: "",
        currency: "",
        radio: true,
        offerPrice: "",
        isFixed: true,
        offerPrice_2: 26012.921,
        offerPriceRate: 0,
        minPrice: 100,
        maxPrice: 1000,
        checkList: [],
        tinymceHtml:
          "<p>1. 订单有效期为15分钟，请您及时在有效期内付款并点击「标记付款已完成」按钮，我才可以释放数字币给您</p><p> 2. 如果您无法在15分钟内完成付款，请点击「延长订单有效期」按钮</p><p> 3. 开始交易后数字币由系统锁定托管，请安心下单</p>"
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    },
    fomatFloat(value, n) {
      var f = Math.round(value * Math.pow(10, n)) / Math.pow(10, n);
      var s = f.toString();
      var rs = s.indexOf(".");
      if (rs < 0) {
        s += ".";
      }
      for (var i = s.length - s.indexOf("."); i <= n; i++) {
        s += "0";
      }
      return s;
    },
    formatTooltip(val) {
      return val / 100;
    }
  },
  mounted() {
    tinymce.init({});
    var user = sessionStorage.getItem("user");
    if (user) {
      this.isLogin = true;
    }
  },
  computed: {
    tableStr1: function() {
      return this.form.isBuy ? this.tableStr1_1 : this.tableStr1_2;
    },
    defaultPrice: function() {
      return parseFloat(
        this.fomatFloat(parseFloat(this.lowestMarketPrice) * 0.94, 3)
      );
    },
    floatPrice: function() {
      return parseFloat(
        this.fomatFloat(
          (this.lowestMarketPrice * (100 + this.form.offerPriceRate)) / 100,
          3
        )
      );
    }
  }
};
</script>
<style scoped>
.tableRow {
  margin-top: 11px;
}
.firstRow {
  margin: 2%;
}
.bigText {
  margin: 0 0 20px;
  border-left: 5px solid #43cb83;
  padding-left: 10px;
  font-size: 28px;
}
.normalText {
  margin: 0 0 10px;
}
.greenText {
  color: #2a985e;
  margin: 5px 0;
}
.greenTextBig {
  color: #2a985e;
  margin: 5px 0;
  font-size: 24px;
}
.greenTextSmall {
  color: #2a985e;
  margin: 5px 0;
  font-size: 10px;
}
.bigText2 {
  margin: 10px 0;
  font-size: 20px;
}
</style>



